import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './CategoryDetail.css';
import { useParams, Link, useNavigate, useLocation } from 'react-router-dom';

const CategoryDetail = () => {
  const { slug } = useParams();
  const [category, setCategory] = useState({});
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [categories, setCategories] = useState([]); // Add categories state
  const navigate = useNavigate();
  const location = useLocation();

  const postsPerPage = 10;

  useEffect(() => {
    const fetchCategory = async () => {
      try {
        const response = await axios.get(`https://meetanshi.com/blog/wp-json/wp/v2/categories?slug=${slug}`);
        if (response.data.length > 0) {
          const categoryData = response.data[0];
          setCategory(categoryData);
        } else {
          console.error('Category not found');
        }
      } catch (error) {
        console.error('Error fetching category:', error);
      }
    };

    const fetchPostsByCategory = async () => {
      try {
        const response = await axios.get(`https://meetanshi.com/blog/wp-json/wp/v2/posts?categories=${category.id}&_embed&per_page=${postsPerPage}&page=${currentPage}`);
        setPosts(response.data);
        const totalPosts = response.headers['x-wp-total'];
        setTotalPages(Math.ceil(totalPosts / postsPerPage));
      } catch (error) {
        console.error('Error fetching posts for category:', error);
      } finally {
        setLoading(false);
      }
    };

    const fetchCategories = async () => {
      try {
        const response = await axios.get('https://meetanshi.com/blog/wp-json/wp/v2/categories');
        const filteredCategories = response.data.filter(category => category.id !== 1); // Exclude 'Uncategorized'
        setCategories(filteredCategories); // Set categories state
      } catch (error) {
        console.error('Error fetching categories:', error);
      }
    };

    fetchCategory();
    fetchPostsByCategory();
    fetchCategories();
  }, [slug, category.id, currentPage]);

  useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    const page = parseInt(queryParams.get('page')) || 1;
    setCurrentPage(page);
  }, [location.search]);

  // Function to handle page click for pagination
  const handlePageClick = (page) => {
    setCurrentPage(page);
    navigate(`?page=${page}`);
  };

  // Function to get the author link for each post
  const getAuthorLink = (post) => {
    const author = post._embedded && post._embedded.author && post._embedded.author[0];
    if (author) {
      const authorImage = author.avatar_urls ? author.avatar_urls['96'] : 'https://via.placeholder.com/50';
      return (
        <Link to={`/author/${author.slug}`} className="author-link">
          <img
            src={authorImage}
            alt={author.name}
            style={{ width: '30px', height: '30px', borderRadius: '50%', marginRight: '10px' }}
          />
          {author.name}
        </Link>
      );
    }
    return 'Unknown Author';
  };

  const handleCategoryClick = (categorySlug) => {
    setCurrentPage(1); // Reset to the first page when changing categories
    navigate(`/category/${categorySlug}`); // Navigate to the new category
  };

  const renderCategoryList = () => {
    return (
      <div className="category-list-filter mb-4">
        <ul className="category-list">
          <li>
            <Link to="/" className="category-link"> {/* Link to homepage */}
              All
            </Link>
          </li>
          {categories.map((cat) => (
            <li
              key={cat.id}
              className={`category-item ${cat.slug === slug ? 'active' : ''}`} // Add active class if category is selected
            >
              <button onClick={() => handleCategoryClick(cat.slug)} className="category-link">
                {cat.name}
              </button>
            </li>
          ))}
        </ul>
      </div>
    );
  };

  const renderPagination = () => {
    const paginationItems = [];
    const range = 1;

    if (currentPage > 1) {
      paginationItems.push(
        <li className="page-item" key="prev">
          <button className="page-link" onClick={() => handlePageClick(currentPage - 1)}>Previous</button>
        </li>
      );
    }

    if (totalPages > 0) {
      paginationItems.push(
        <li className={`page-item ${currentPage === 1 ? 'active' : ''}`} key={1}>
          <button className="page-link" onClick={() => handlePageClick(1)}>1</button>
        </li>
      );
    }

    if (currentPage > range + 2) {
      paginationItems.push(<li className="page-item disabled" key="ellipsis1"><span className="page-link">...</span></li>);
    }

    for (let page = Math.max(2, currentPage - range); page <= Math.min(totalPages - 1, currentPage + range); page++) {
      paginationItems.push(
        <li className={`page-item ${currentPage === page ? 'active' : ''}`} key={page}>
          <button className="page-link" onClick={() => handlePageClick(page)}>{page}</button>
        </li>
      );
    }

    if (currentPage < totalPages - range - 1) {
      paginationItems.push(<li className="page-item disabled" key="ellipsis2"><span className="page-link">...</span></li>);
    }

    if (totalPages > 1) {
      paginationItems.push(
        <li className={`page-item ${currentPage === totalPages ? 'active' : ''}`} key={totalPages}>
          <button className="page-link" onClick={() => handlePageClick(totalPages)}>{totalPages}</button>
        </li>
      );
    }

    if (currentPage < totalPages) {
      paginationItems.push(
        <li className="page-item" key="next">
          <button className="page-link" onClick={() => handlePageClick(currentPage + 1)}>Next</button>
        </li>
      );
    }

    return paginationItems;
  };

  return (
    <div>
      <div className="container-lg container-fluid-md">
        <div className="row">
          <div className="col-md-12">
            <div className="cat-hero-section">
              <div className="home-hero-wrapper text-center">
                <h1 className="home-heading">{category.name}</h1>
                <p>{category.description}</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Render Category Filter */}
      {renderCategoryList()}

      {loading ? (
        <p>Loading...</p>
      ) : posts.length > 0 ? (
        <div className="row mt-5 regular-post-main">
          {posts.map((post) => (
            <div className="col-md-6 text-left regular-post-col mb-5" key={post.id}>
              <div className="regular-post-wrapper">
                <Link className="post-title-link" to={`/post/${post.slug}`}>
                  <h3 className="post-title" dangerouslySetInnerHTML={{ __html: post.title.rendered }}></h3>
                </Link>
                <p dangerouslySetInnerHTML={{ __html: post.excerpt.rendered }}></p>
                <p className="author-name-date">
                  <span>{getAuthorLink(post)}</span>
                  <span>{new Date(post.date).toLocaleDateString('en-US', { year: 'numeric', month: 'short', day: 'numeric' })}</span>
                </p>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <p>No posts found.</p>
      )}

      {/* Pagination Section */}
      {totalPages > 1 && (
        <nav aria-label="Page navigation" className="pagination-main mt-3">
          <ul className="pagination justify-content-center">
            {renderPagination()}
          </ul>
        </nav>
      )}
    </div>
  );
};

export default CategoryDetail;
