import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { useParams, Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'; // Import FontAwesome
import { faChevronDown, faChevronUp } from '@fortawesome/free-solid-svg-icons'; // Import icons
import './PostDetail.css'; // Assuming you have a CSS file for styles


const PostDetail = () => {
  const { slug } = useParams(); // Get post slug from URL
  const [post, setPost] = useState(null);
  const [author, setAuthor] = useState(null);
  const [comments, setComments] = useState([]);
  const [loadingComments, setLoadingComments] = useState(true);
  const [headings, setHeadings] = useState([]);
  const [comment, setComment] = useState('');
  const [authorName, setAuthorName] = useState('');
  const [authorEmail, setAuthorEmail] = useState('');
  const [commentError, setCommentError] = useState('');
  const [commentSuccess, setCommentSuccess] = useState('');
  const [tocVisible, setTocVisible] = useState(false); // State for TOC visibility
  const [replyToCommentId, setReplyToCommentId] = useState(null);
  const [replyContent, setReplyContent] = useState(''); // New state for reply content
  const [acfFields, setAcfFields] = useState({});
  const [categories, setCategories] = useState([]);
  const [showShopifyCTA, setShowShopifyCTA] = useState(false);
  const [showMagentoCTA, setShowMagentoCTA] = useState(false);
  const [selectedSidebar, setSelectedSidebar] = useState('');
  const contentRef = useRef(null);

  const calculateReadingTime = (content) => {
    const wordCount = content ? content.split(/\s+/).filter((word) => word).length : 0;
    const readingTime = Math.ceil(wordCount / 200);
    return `${readingTime} min`;
  };

  // Fetch post details by slug
  useEffect(() => {
    if (!slug) return;

    axios
      .get(`https://meetanshi.com/blog/wp-json/wp/v2/posts?slug=${slug}&_embed&_=${new Date().getTime()}`)
      .then((response) => {
        const postData = response.data[0];
        setPost(postData);

        // Get author information from the embedded response
        const authorData = postData._embedded?.author?.[0];
        if (authorData) setAuthor(authorData);

        // Fetch categories for the post
        setCategories(postData.categories);
        
        // Check if the post belongs to Shopify or Magento category
        const shopifyCategoryId = 5731;  // Shopify category ID
        const magentoCategoryId = 16;    // Magento category ID
        const hasShopifyCategory = postData.categories.includes(shopifyCategoryId);
        const hasMagentoCategory = postData.categories.includes(magentoCategoryId);
        
        // Set Shopify and Magento CTA visibility
        setShowShopifyCTA(hasShopifyCategory);
        setShowMagentoCTA(hasMagentoCategory);
      })
      .catch((error) => {
        console.error('Error fetching post:', error);
      });
  }, [slug]);

  // Fetch ACF fields for the selected category
  useEffect(() => {
    if (categories.length > 0) {
      const categoryAcfUrl = `https://meetanshi.com/blog/wp-json/wp/v2/categories/${categories[0]}`;
      axios.get(categoryAcfUrl)
        .then((response) => {
          setAcfFields(response.data.acf);
        })
        .catch((error) => {
          console.error('Error fetching ACF fields:', error);
        });
    }
  }, [categories]);

  // Render Shopify or Magento sidebar content based on the category
  const renderCategorySidebar = () => {
    const ctaData = showShopifyCTA ? acfFields : showMagentoCTA ? acfFields : null;

    if (!ctaData) return null;

    const { cta_image, cta_category_title, cta_category_description, cta_button } = ctaData;
    const relAttr = showShopifyCTA ? 'nofollow' : 'follow'; // Shopify is nofollow, Magento is follow

    return (
      <div className="right-sidebar-cta-section mt-3">
        <div className={`sidebar-cta ${showShopifyCTA ? 'shopify-cta' : 'magento-cta'}`}>
          <div className="sidebar-wrapper text-center">
            {cta_image && (
              <img
                src={cta_image.url}
                alt={cta_image.alt}
                className="img-fluid"
              />
            )}
            {cta_category_title && <p className="cat-cta-title">{cta_category_title}</p>}
            {cta_category_description && <p>{cta_category_description}</p>}
            {cta_button && (
              <a
                href={cta_button.url}
                target={cta_button.target}
                className="sidebar-cat-cta-link"
                rel={relAttr}
              >
                {cta_button.title}
              </a>
            )}
          </div>
        </div>
      </div>
    );
  };


  useEffect(() => {
    const fetchPostDetails = async () => {
      try {
        // Fetch post data using slug
        const response = await fetch(`https://meetanshi.com/blog/wp-json/wp/v2/posts?slug=${slug}&_embed&_=${new Date().getTime()}`);
        const postData = await response.json();

        // Check if postData is not empty
        if (postData.length > 0) {
          const categories = postData[0]._embedded['wp:term'][0] || []; // Adjusted for the _embed structure
  
          let shopifyCategory = false;
          let magentoCategory = false;
  
          // Check categories
          categories.forEach((category) => {
            if (category.slug === 'shopify') {
              shopifyCategory = true;
            }
            if (category.slug === 'magento') {
              magentoCategory = true;
            }
          });
  
          // Set selected sidebar based on categories
          if (shopifyCategory) {
            setSelectedSidebar('hire-shopify-experts');
          } else if (magentoCategory) {
            setSelectedSidebar('magento-upgrade');
          }
        }
      } catch (error) {
        console.error('Error fetching post details:', error);
      }
    };
  
    fetchPostDetails();
  }, [slug]);

  // Fetch comments for the post
  useEffect(() => {
    if (post) {
      const fetchComments = async () => {
        try {
          const response = await axios.get(`https://meetanshi.com/blog/wp-json/wp/v2/comments?post=${post.id}`);
          // Sorting comments in descending order
          const sortedComments = response.data.sort((a, b) => new Date(b.date) - new Date(a.date));
          setComments(sortedComments);
        } catch (error) {
          console.error('Error fetching comments:', error);
        } finally {
          setLoadingComments(false);
        }
      };

      fetchComments();
    }
  }, [post]);

  // Fetch sidebar HTML
  // useEffect(() => {
  //   if (post && post.selected_sidebar) {
      
  //     axios
  //       .get(`https://meetanshi.com/blog/wp-json/wp/v2/sidebar/${post.selected_sidebar}`)
        
  //       .then((response) => {
         
  //         setSidebarHtml(response.data);
  //         console.log('Error fetching comments:' ,response.data);
  //       })
  //       .catch((error) => {
  //         console.error('Error fetching sidebar:', error);
  //       });
  //   }
  // }, [post]);

  // Extract headings from post content
  useEffect(() => {
    if (post) {
      const contentElement = contentRef.current;
      if (contentElement) {
        const extractedHeadings = Array.from(contentElement.querySelectorAll('h1, h2, h3, h4, h5, h6')).map(
          (heading) => {
            const id = heading.innerText.replace(/\s+/g, '-').toLowerCase();
            heading.id = id;
            return {
              id,
              text: heading.innerText,
              level: parseInt(heading.tagName.replace('H', ''), 10),
            };
          }
        );
        setHeadings(extractedHeadings);
      }
    }
  }, [post]);

  // Scroll to heading section
  const scrollToHeading = (id) => {
    const element = document.getElementById(id);
    if (element) {
      window.scrollTo({
        top: element.offsetTop - 80,
        behavior: 'smooth',
      });
    }
  };

  // Handle new comment submission
  const handleCommentSubmit = (e) => {
    e.preventDefault();
    const newComment = {
      post: post.id,
      author_name: authorName,
      author_email: authorEmail,
      content: replyContent || comment, // Use replyContent for replies, or comment for new comments
    };
  
    axios
      .post('https://meetanshi.com/blog/wp-json/wp/v2/comments', newComment)
      .then((response) => {
        setComments([response.data, ...comments]); // Prepend new comment
        setComment(''); // Clear new comment content
        setReplyContent(''); // Clear reply content
        setAuthorName('');
        setAuthorEmail('');
        setCommentSuccess('Your comment has been submitted!');
        setCommentError('');
        setReplyToCommentId(null); // Clear reply to comment
      })
      .catch((error) => {
        setCommentError('Failed to submit comment.');
        setCommentSuccess('');
      });
  };  

  // Toggle TOC visibility
  const toggleToc = () => {
    setTocVisible(!tocVisible);
  };

  // Helper function to format date
  const formatDate = (dateString) => {
    const options = { year: 'numeric', month: 'long', day: 'numeric' };
    return new Date(dateString).toLocaleDateString(undefined, options);
  };

    // Handle reply button click
    const handleReplyClick = (commentId) => {
      setReplyToCommentId(commentId); // Set reply to comment ID
    };

  // Recursive function to display nested comments
  const renderComments = (commentList, parentId = 0) => {
    return commentList
      .filter((comment) => comment.parent === parentId)
      .map((comment) => (
        <div key={comment.id} className={`comment-item ${comment.parent !== 0 ? 'nested-comment' : ''}`}>
          <div className="comment-avatar">
            <div className="comment-avatar-img">
              <span>{comment.author_name.charAt(0)}</span>
            </div>
          </div>
          <div className="comment-body">
            <h5>{comment.author_name}</h5>
            <p className="comment-date">on {formatDate(comment.date)}</p>
            <div dangerouslySetInnerHTML={{ __html: comment.content.rendered }} />
            <button className="comment-reply-button" onClick={() => handleReplyClick(comment.id)}>
              Reply
            </button>
            {/* Display reply form under the reply button if this comment is being replied to */}
            {replyToCommentId === comment.id && (
              <form onSubmit={handleCommentSubmit} className="reply-form">
                  <div className="row">
                  <div className="col-md-12">
              <textarea
                placeholder="Your reply"
                value={replyContent}
                onChange={(e) => setReplyContent(e.target.value)} // Use replyContent
                required
              />
               </div>
               <div className="col-md-6">
              <input
                type="text"
                placeholder="Your name"
                value={authorName}
                onChange={(e) => setAuthorName(e.target.value)}
                required
              />
               </div>
               <div className="col-md-6">
              <input
                type="email"
                placeholder="Your email"
                value={authorEmail}
                onChange={(e) => setAuthorEmail(e.target.value)}
                required
              />
              </div>
              <div className="col-md-12">
              <button type="submit">Submit Reply</button>
              </div>
              </div>
            </form>
            
            )}
            {renderComments(commentList, comment.id)} {/* Recursively render replies */}
          </div>
        </div>
      ));
  };

  if (!post) {
    return <div>Loading...</div>;
  }

  // Breadcrumbs Logic
  const renderBreadcrumbs = () => {
    return (
      <ul className="breadcrumbs">
        <li>
          <Link to="/">Blog</Link>
        </li>
        <li>
          <span className="home-heading" dangerouslySetInnerHTML={{ __html: post.title.rendered.replace(/&amp;/g, '&') }} ></span>
        </li>
      </ul>
    );
  };

  return (
    <div className="post-detail-container">
      <div className="post-content">
        <div className="container-lg container-fluid-md">
          <div className="row">
            <div className="col-md-12">
              <div className="cat-hero-section">
                <div className="home-hero-wrapper text-center">
                  {/* Breadcrumbs */}
                  {renderBreadcrumbs()}
                  <h1 className="home-heading" dangerouslySetInnerHTML={{ __html: post.title.rendered.replace(/&amp;/g, '&') }} ></h1>
                  <div className="post-author-readingtime">
                  {post && <p className="reading-time">{calculateReadingTime(post.content.rendered)} read <span class="hero-ellipse"></span></p>}
                  {author && <p className="author-of-post">By {author.name} <span class="hero-ellipse"></span></p>}
                  <p>{new Date(post.date).toLocaleDateString('en-US', { year: 'numeric', month: 'short', day: 'numeric' })}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="container mt-3">
          <div className="row">
            <div className="col-md-9">
              {/* Post Content */}
              <div className="blog-content-wrapper" ref={contentRef} dangerouslySetInnerHTML={{ __html: post.content.rendered }} />
              {/* Comments Section */}
              <div className="comments-section">
              <h3 className="comment-section-title">Discussion</h3>
                <div className="comment-section-wrapper">
                  
                    {loadingComments ? (
                      <p>Loading comments...</p>
                    ) : comments.length > 0 ? (
                      <div className="comments-list">{renderComments(comments)}</div>
                    ) : (
                      <p className="mt-2">No comments yet.</p>
                    )}
                </div>
              </div>
              {/* Leave a Reply Form */}
              <div className="leave-reply-form">
                <h3>Leave a Reply</h3>
                {commentSuccess && <p className="success-message">{commentSuccess}</p>}
                {commentError && <p className="error-message">{commentError}</p>}
                <form onSubmit={handleCommentSubmit}>
                  <div className="row">
                    <div className="col-md-12">
                      <textarea
                        id="comment"
                        value={comment}
                        placeholder="Add Comment"
                        onChange={(e) => setComment(e.target.value)}
                        required
                      />
                    </div>
                    <div className="col-md-6">
                      <input
                        id="author-name"
                        type="text"
                        placeholder="Your name"
                        value={authorName}
                        onChange={(e) => setAuthorName(e.target.value)}
                        required
                      />
                    </div>
                    <div className="col-md-6">
                      <input
                        id="author-email"
                        type="email"
                        placeholder="Your email"
                        value={authorEmail}
                        onChange={(e) => setAuthorEmail(e.target.value)}
                        required
                      />
                    </div>
                    <div className="col-md-12">
                      <button type="submit" className="submit-comment-btn">Post Comment</button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
            <div className="col-md-3">
              {/* Table of Contents */}
              <div className="toc">
                <button className="toc-toggle" onClick={toggleToc}>
                  {tocVisible ? (
                    <>
                      Table of Content <FontAwesomeIcon icon={faChevronUp} /> 
                    </>
                  ) : (
                    <>
                      Table of Content <FontAwesomeIcon icon={faChevronDown} />
                    </>
                  )}
                </button>
                {tocVisible && (
                  <ul className="toc-content">
                    {headings.map((heading) => (
                      <li key={heading.id} className={`toc-level-${heading.level}`}>
                        <button onClick={() => scrollToHeading(heading.id)}>{heading.text}</button>
                      </li>
                    ))}
                  </ul>
                )}
              </div>

              {/* Author Information */}
              <div className="author-info text-center">
                {author && (
                  <>
                    {author.avatar_urls && author.avatar_urls[96] ? (
                      <img
                        src={author.avatar_urls[96]}
                        alt={`${author.name}'s avatar`}
                        width="96"
                        height="96"
                      />
                    ) : (
                      <p>No avatar available.</p>
                    )}
                    <h4>{author.name}</h4>
                    
                    {/* Trim description to 30 words and add 'Read More' */}
                    {author.description ? (
                      <>
                        <p>
                          {author.description.split(' ').slice(0, 30).join(' ')}...{' '}
                          <a href={`/author/${author.slug}`}>Read more</a>
                        </p>
                      </>
                    ) : (
                      <p>No description available.</p>
                    )}
                  </>
                )}
              </div>
              {/* Sidebar HTML */}
                {renderCategorySidebar()}
                <div className="right-sidebar-cta-section">
                  {selectedSidebar === 'hire-shopify-experts' && (
                    <div>
                      {/* Replace this with your Shopify sidebar content */}
                      <h2>Hire Shopify Experts</h2>
                      {/* Example content */}
                      <p>Content related to hiring Shopify experts goes here.</p>
                    </div>
                  )}
                  {selectedSidebar === 'magento-upgrade' && (
                    <div>
                      {/* Replace this with your Magento sidebar content */}
                      <h2>Magento Upgrade</h2>
                      {/* Example content */}
                      <p>Content related to Magento upgrades goes here.</p>
                    </div>
                  )}
                </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PostDetail;
