import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './Footer.css'; // Optional: Create a CSS file for styling

const Footer = () => {
  const [footerData, setFooterData] = useState({
    footer_logo: null,
    address_india: '',
    address_usa: '',
    phone: '',
    social_ratings: [],
    net_zero_website: null,
    social_icons: [],
    copyright: '',
    footer_links: [],
  });

  const [quickLinks, setQuickLinks] = useState([]);
  const [serviceMenu, setServiceMenu] = useState([]);

  // Fetch footer data from custom WordPress REST API
  useEffect(() => {
    const fetchFooterData = async () => {
      try {
        const response = await axios.get('https://meetanshi.com/blog/wp-json/custom/v1/footer-data');
        setFooterData(response.data);
      } catch (error) {
        console.error('Error fetching footer data:', error);
      }
    };

    fetchFooterData();
  }, []);

  // Fetch footer menus data
  useEffect(() => {
    const fetchFooterMenus = async () => {
      try {
        const response = await axios.get('https://meetanshi.com/blog/wp-json/footer-menus/v1/get-menus');
        setQuickLinks(response.data.quick_links);
        setServiceMenu(response.data.service_menu);
      } catch (error) {
        console.error('Error fetching footer menus:', error);
      }
    };

    fetchFooterMenus();
  }, []);

  // Render menu items
  const renderMenuItems = (menuItems) => {
    return menuItems.map((item) => (
      <li key={item.id}>
        <a href={item.url}>{item.title}</a>
      </li>
    ));
  };

  // Render social icons
  const renderSocialIcons = () => {
    return footerData.social_icons.map((icon, index) => (
      <li key={index}>
        <a target="_blank" rel="noopener noreferrer" href={icon.url} title={icon.name}>
          <i className={icon.icon}></i> {/* Assuming icon.icon contains the full Font Awesome class */}
        </a>
      </li>
    ));
  };

  return (
    <footer className="site-footer">
      <div className="container mb-5">
        <div className="row justify-content-between">
          <div className="col-auto">
            {/* Footer Logo */}
            {footerData.footer_logo && (
              <div className="footer-logo">
                <a href="/">
                  <img
                    src={footerData.footer_logo.url}
                    alt={footerData.footer_logo.alt}
                    width={footerData.footer_logo.width}
                    height={footerData.footer_logo.height}
                  />
                </a>
              </div>
            )}
            {/* Address India */}
            {footerData.address_india && (
              <div className="footer-address">
                <div className="d-flex align-items-start">
                  <img className="address-icon" src="/images/location-icon.svg" alt="Location Icon" height="25" width="24" />
                  <div>
                    <p className="footer-address-title">Bhavnagar<sup>HQ</sup></p>
                    <p className="footer-address-text">{footerData.address_india}</p>
                  </div>
                </div>
              </div>
            )}
            {/* Address USA */}
            {footerData.address_usa && (
              <div className="footer-address">
                <div className="d-flex align-items-start">
                  <img className="address-icon" src="/images/location-icon.svg" alt="Location Icon" height="25" width="24" />
                  <div>
                    <p className="footer-address-title">USA Representative Office</p>
                    <p className="footer-address-text">{footerData.address_usa}</p>
                  </div>
                </div>
              </div>
            )}
            {/* Phone */}
            {footerData.phone && (
              <div className="footer-phone">
                <div className="d-flex align-items-start">
                  <img className="address-icon" src="/images/phone-icon.svg" alt="Phone Icon" height="25" width="24" />
                  <div>
                    <a className="footer-phone" href={`tel:${footerData.phone}`}>{footerData.phone}</a>
                  </div>
                </div>
              </div>
            )}
          </div>

          {/* Quick Links */}
          <div className="col-auto">
            <div className="footer-col-wrapper">
              <p className="footer-menu-title">Quick Links</p>
              <ul className="footer-menu">
                {renderMenuItems(quickLinks)}
              </ul>
            </div>
          </div>

          {/* Customer Service */}
          <div className="col-auto">
            <div className="footer-col-wrapper">
              <p className="footer-menu-title">Customer Service</p>
              <ul className="footer-menu">
                {renderMenuItems(serviceMenu)}
              </ul>
            </div>
          </div>

          {/* Client's Love Section */}
          <div className="col-auto">
            <div className="footer-col-wrapper">
              <p className="footer-menu-title">Client's Love</p>
              {/* Social Ratings */}
              {footerData.social_ratings && footerData.social_ratings.length > 0 && (
                <ul className="footer-social-rating">
                  {footerData.social_ratings.map((rating, index) => (
                    <li key={index}>
                      <a target="_blank" href={rating.url} rel="noopener noreferrer">
                        <picture>
                          <img
                            src={rating.image.url}
                            alt={rating.image.alt}
                            width={rating.image.width}
                            height={rating.image.height}
                          />
                        </picture>
                      </a>
                    </li>
                  ))}
                </ul>
              )}
              {/* Social Media Icons */}
              <div className="social-media">
                <ul>
                  {renderSocialIcons()}
                </ul>
              </div>
              {/* Net Zero Website */}
              {footerData.net_zero_website && (
                <div className="net-zero-website">
                  <a href={footerData.net_zero_website.url} target="_blank" rel="noopener noreferrer">
                    <img
                      src={footerData.net_zero_website.image.url}
                      alt={footerData.net_zero_website.image.alt}
                      height={footerData.net_zero_website.image.height}
                      width={footerData.net_zero_website.image.width}
                      loading="lazy"
                    />
                  </a>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
       {/* Footer Copy Section */}
       <div className="footer-copy">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-md-6">
                {/* Display copyright if available */}
                {footerData.copyright && (
                  <div className="footer-copy-text">
                    {/* {footerData.copyright} */}
                    <p>Copyright  2017-2024 by Meetanshi Technologies LLP. All Rights Reserved.</p>
                  </div>
                )}
              </div>
              <div className="col-md-6 text-end">
                {/* Display footer links if available */}
                {footerData.footer_links && footerData.footer_links.length > 0 && (
                  <ul className="footer-copy-links">
                    {footerData.footer_links.map((link, index) => (
                      <li key={index}>
                        <a href={link.link.url} target={link.link.target}>
                          {link.link.title}
                        </a>
                      </li>
                    ))}
                  </ul>
                )}
              </div>
            </div>
          </div>
        </div>
    </footer>
  );
};

export default Footer;
