import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useParams, Link } from 'react-router-dom';

const AuthorDetail = () => {
  const { slug } = useParams();
  const [author, setAuthor] = useState(null);
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const postsPerPage = 6; // Adjust this number as needed

  // Effect to fetch author details based on slug
  useEffect(() => {
    const fetchAuthorDetails = async () => {
      setLoading(true);
      setError('');
    
      try {
        // Fetch author details from the custom endpoint using POST
        const authorResponse = await axios.post('https://meetanshi.com/blog/wp-json/custom/v1/author', {
          slug: slug // Send the slug in the request body
        });
    
        if (authorResponse.data && authorResponse.data.id) {
          setAuthor(authorResponse.data); // Set author from response
          fetchAuthorPosts(authorResponse.data.id); // Fetch posts with author ID
        } else {
          setError('Author not found.');
        }
      } catch (err) {
        console.error('Error fetching author details:', err);
        setError('Failed to load author details.');
      } finally {
        setLoading(false);
      }
    };
    

    fetchAuthorDetails(); // Call the function to fetch author details
  }, [slug]);

  const fetchAuthorPosts = async (authorId) => {
    console.log('Fetching posts for authorId:', authorId);
    if (!authorId) {
      console.error('No author ID provided');
      setError('No author ID provided.');
      return;
    }

    setLoading(true); // Start loading state

    try {
      const url = `https://meetanshi.com/blog/wp-json/wp/v2/posts?author=${authorId}&_embed&_=${new Date().getTime()}`;
      console.log('Fetching from URL:', url);

      const postsResponse = await axios.post(url);
      console.log('Author posts response:', postsResponse); // Log the whole response object

      if (Array.isArray(postsResponse.data) && postsResponse.data.length > 0) {
        setPosts(postsResponse.data);
      } else {
        console.warn('Posts response is empty or not an array:', postsResponse.data);
        setPosts([]); // Set to empty array if posts response is invalid
      }
    } catch (err) {
      console.error('Error fetching posts by author:', err);
      if (err.response) {
        console.error('Response Status:', err.response.status);
        console.error('Response Data:', err.response.data);
        setError(`Failed to load posts. Status: ${err.response.status}`);
      } else {
        setError('Failed to load posts. Please try again later.');
      }
    } finally {
      setLoading(false); // End loading state
    }
};


  // Check loading state
  if (loading) {
    return <p>Loading...</p>;
  }

  // Check for errors
  if (error) {
    return <p>{error}</p>;
  }

  // Check if author is not found
  if (!author) {
    return <p>No author found.</p>;
  }

  // Pagination logic
  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  const currentPosts = posts.slice(indexOfFirstPost, indexOfLastPost);
  const totalPages = Math.ceil(posts.length / postsPerPage);

  return (
    <div className="author-detail">
      <h2>{author.name}</h2>
      <img src={author.avatar_urls?.[96] || 'https://via.placeholder.com/150'} alt={author.name} className="single-author-image img-fluid" />
      <div dangerouslySetInnerHTML={{ __html: author.description || 'No description available.' }}></div>
      
      <h3>Posts by {author.name}:</h3>
      {Array.isArray(currentPosts) && currentPosts.length > 0 ? (
        <div className="row">
          {currentPosts.map((post) => (
            
            <div className="col-md-6" key={post.id}>
              <img 
                src={post._embedded?.['wp:featuredmedia']?.[0]?.source_url || 'https://via.placeholder.com/300'} 
                alt={post.title.rendered} 
                className="post-image img-fluid mb-2" 
              />
              <Link to={`/post/${post.slug}`}><h5>{post.title.rendered}</h5></Link>
              <p>{post._embedded?.['wp:term']?.[0]?.map((category) => category.name).join(', ') || 'Uncategorized'}</p>
            </div>
          ))}
        </div>
      ) : (
        <p>No posts found by {author.name}.</p>
      )}

      {/* Pagination Controls */}
      {totalPages > 1 && (
        <nav>
          <ul className="pagination justify-content-center">
            {[...Array(totalPages)].map((_, index) => (
              <li key={index} className={`page-item ${currentPage === index + 1 ? 'active' : ''}`}>
                <button 
                  className="page-link" 
                  onClick={() => setCurrentPage(index + 1)}
                >
                  {index + 1}
                </button>
              </li>
            ))}
          </ul>
        </nav>
      )}
    </div>
  );
};

export default AuthorDetail;
